* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'open sans', sans-serif;
  font-size: 20px;
}
div.app {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    min-height: 150vh;
    width: 90%;
    margin: 0 auto;
}

header {
    -webkit-flex-basis: 50vh;
            flex-basis: 50vh;
}

main {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex-grow: 1;
            flex-grow: 1;
}

nav {
    min-height: 50px;
}

section {
    -webkit-flex-grow: 1;
            flex-grow: 1;
}

footer {
    background-color: black;
    color: white;
    padding: 10px 0;
    text-align: center;
    text-transform: uppercase;
}
header {
    position: relative;
    overflow: hidden;
}

header img {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
}
nav {
    /* padding: 0 30px 30px 0; */
}

ul {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    line-height: 50px;
    list-style-type: none;
}

nav a {
    display: block;
    border: 2px solid black;
    padding: 0 20px;
    text-decoration: none;
    color: black;
    text-transform: uppercase;
    font-weight: bold;
    -webkit-transition: .2s;
    transition: .2s;
}

nav a:hover {
    background-color: black;
    color: white;
}

nav a.active {
    background-color: black;
    color: white;
}
section {
    position: relative;
}

img.startImg {
    position: absolute;
    height: 80%;
    bottom: 0%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

section h1 {
    text-align: center;
    margin-top: 100px;
}

div.male {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    height: 35vh;
    margin-top: 20px;
    margin-bottom: 20px;
}

div.photo {
    position: relative;
    -webkit-flex-basis: 20vw;
            flex-basis: 20vw;
}

div.photo img {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    height: 25vh;
    border-radius: 2%;
}

div.info {
    -webkit-flex-basis: 30vw;
            flex-basis: 30vw;
    padding: 30px 0 0 20px;
}
.contact textarea {
    font-size: 16px;
    width: 80%;
    min-height: 200px;
    margin: 20px 0 0 10%;
    line-height: 1.6;
}

.contact button {
    display: block;
    border: 2px solid black;
    background-color: white;
    margin: 0 10% 0 auto;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 20px;
}
